<template>
	<view class="user-setting">
		<view class="list-box boxsiz">
			<navigator url="/pages/user/nickName" class="box row verCenter bothSide boxsiz" hover-class="none">
				<text>昵称</text>
				<view class="nick">
					<text class="nickname" style="padding-right: 10rpx;">{{nickname}}</text>
					<icon class="ico-1"></icon>
				</view>
				
			</navigator>
			<view class="box row verCenter bothSide boxsiz" hover-class="none" @click="choose">
				<text>修改头像</text>
				<view class="pic">
					<image :src="imgUrl" mode="aspectFill" class="userImage"></image>
					<icon class="ico-1"></icon>
				</view>
			</view>
			<!-- <navigator url="/pages/user/certification" class="box row verCenter bothSide boxsiz" hover-class="none">
				<text>实名认证</text>
				<icon class="ico-1"></icon>
			</navigator>
			<navigator url="/pages/user/bank" class="box row verCenter bothSide boxsiz" hover-class="none">
				<text>银行卡</text>
				<icon class="ico-1"></icon>
			</navigator> -->
			<navigator url="/pages/user/payPassword" class="box row verCenter bothSide boxsiz" hover-class="none">
				<text>支付密码</text>
				<icon class="ico-1"></icon>
			</navigator>
			<navigator class="box row verCenter bothSide boxsiz" hover-class="none" @click="logout">
				<text>退出登录</text>
				<icon class="ico-1"></icon>
			</navigator>
		</view>
		<!-- <view class="btn">保存</view> -->
	</view> 
</template>

<script>
	import {
		API
	} from '@/util/api.js';
	export default {
		data() {
			return {
				imgUrl: '',
				nickname:''
			};
		},
		onShow() {
			this.getData();
		},
		onLaunch: {},
		methods: {
			getData() {
				this.request(API.getUserAccountData).then(res => {
					if (res.code === 10000) {
						console.log(res)
						this.imgUrl = res.data.avatar;
						this.nickname=res.data.nickname
					}
				});
			},
			logout() {
				uni.showModal({
					title: '提示',
					content: '确定要退出吗',
					success: function(res) {
						if (res.confirm) {
							uni.removeStorage({
								key: 'token',
								success: function(res) {
									uni.switchTab({
										url: '/pages/index/index'
									});
								}
							});
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			},
			choose() {
				uni.chooseImage({
					count: 1,
					sizeType: ['original', 'compressed'],
					sourceType: ['album', 'camera'],
					success: chooseImageRes => {
						const tempFilePaths = chooseImageRes.tempFilePaths;
						uni.showLoading({
							title: ''
						});
						uni.uploadFile({
							url: API.updateHeadUrl,
							filePath: tempFilePaths[0],
							name: 'file',
							header: {
								"Authorization": uni.getStorageSync('token'),
							},
							success: uploadFileRes => {
								let data = JSON.parse(uploadFileRes.data);
								uni.hideLoading();
								if (data.code === 10000) {
									this.imgUrl = tempFilePaths[0]
								} else {
									uni.showToast({
										title: data.msg,
										icon: 'none',
										duration: 2000
									});
								}
							}
						});
					}
				});
			}
		}
	};
</script>

<style lang="scss">
	@import '../../assets/css/user/setting.scss';
</style>
